@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #FFF;
  text-align: center;
}

div#root{
  height: 100% !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body, html {
  height: 100%;
}
.forecast{
  max-width: 500px;
  width: 100%;
  min-height:100%;
  margin: 0 auto;
  font-family: 'Open Sans', sans-serif;
  height: 100%;
  overflow: visible;
}

.date-headline{
  background-color: #3eb1df;
  height: 3em;
  padding: 1em;
  font-weight: 300;
}

.weather{
  background-color: #21a5da;
  min-height: 100% ;
  font-weight: 300;
  width: 100%;
  position: relative;
}

.weather .weather-topic{
  font-size: 2em;
  padding-top: 1em;
}

.weather .weather-icon{
  margin: 0 auto;
}

.weather-icon div{
  margin: 0 auto;
  border-radius: 50%;
}

.weather .weather-temp{
  font-size: 10em;
  margin-top: -0.4em;
  margin-bottom: -0.2em;
  font-family: 'Open Sans', sans-serif;
}

.weather .weather-temp .circle{
  background: #FFF;
  border-radius: 50%;
  height:50px;
  width:50px;
}

.weather .weather-city{
  font-size: 1.5em;
  margin-bottom: 150px;
}
/*Circles*/
.circle1{
  background-color: #25a7db;
  height: 19em;
  width: 19em;
}
.circle2{
  background-color: #30abdd;
  height: 17em;
  width: 17em;
}
.circle3{
  background-color: #3fb1df;
  height: 15em;
  width: 15em;
}
.circle4{
  background-color: #50b8e2;
  height: 13em;
  width: 13em;
}
.circle5{
  background-color: #60bee5;
  height: 11em;
  width: 11em;
}

.flex-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  position: relative;
}
/*Date Button*/

.buttons{
  margin-top: -120px;
  width: 100%;
  background-color: #3eb1df;
}
.buttons .row{
  margin-right: 0;
  margin-left: 0;
  position: relative;
  bottom: 0;
  width: 100%;
}

.button-icon {
  width: 60%;
  margin: 0 auto;
}

.buttons .row .col:hover{
  background-color: #1c9dd1;
}

.buttons .row .col{
  padding: 0;
}

.weather-info{
  display: inline-block;
  width: 100%;
}

.slider{
  position: relative;
  margin: 0 auto;
  overflow: hidden;
  white-space: nowrap;
}
